.about-container {
    height:450px;
    width: 100%;
}

.about {
    width:30%;
    height: 70%;
    margin: 0 auto;
    padding:30px;
    display: flex;
}

.about-container .about img {
    float: right;
    width:200px;
}

@media (max-width:1000px) {
    .about {
        width: 60%;
    }
    .about img {
        width:150px;
    }
}