.container {
    padding:5px;
    background-color: white;
    margin:0 auto;
    width:800px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    top:-70px;
   
}
.container .item {
    margin:5px;
    height:220px;
    overflow: hidden;
}

.container img {
    width:390px;
    height:220px;
    transition: .5s;
}

.container h2 {
    padding-left: 15px;
    position:relative;
    bottom:50px;
    color:white;
}

.container .h2 {
    width:50px;
    bottom:80px;
}

.container a {
    text-decoration: none;
}

.container img:hover {
    transform: scale(1.1);
}

@media (max-width:1000px) {
    .container {
        width:400px;
    }
    .container .item {
        height:150px;
    }
    .container img {
        width: 190px;
        height:150px;
    }
    .container h2{
        font-size: 1em;
    }


}