.footer {
    width: 100%;
    background-position-y: bottom;
    background-size:cover;
    height:70px;
    margin-top: auto;
}

.overlay-footer {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
}

.nav-bar-footer {
    margin:0 auto;
    width:60%;
    text-align:end;
    padding-right: 15px;
    position: relative;
    top:20px;
}

.nav-bar-footer a {
    padding:10px;
    text-decoration: none;
    font-size: small;
    color:rgb(212, 211, 211);
}

.nav-bar-footer a:hover, a:focus {
    color:white;
}
